::selection {
    background-color: $clr1;
    color: $clr3;
}

* {
    box-sizing: border-box;
}

html {
    font-family: Bahnschrift, sans-serif;
    color: $clr3;
    line-height: 1.8;
    line-height: lh(26px);

    @media (max-width: 1300px) {
        font-size: 13px;
    }
}

body {
    margin: 0;
    background-color: white;
    height: 100vh;
    font-family: Helmet;
    &.hide-scrol-bar{
        overflow: hidden;
    }  
    @include for-normal-layout {
        &.sidebar-open {
            overflow: hidden;
        }
    }
}

body.modal-open {
    position: fixed;
    z-index: 0;
    height: 100vh;
    overflow-y: hidden;
}

.dark-screen{
    position: fixed;
    height: 100%;
    width: 100%; 
    z-index: 1;
    background-color: $clr4;
    opacity: 0.5;
    filter: grayscale(1);
    display: none;
}


h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: 500;
    margin: 0 0 1rem;
    line-height: lh(40px, 29px);
}

h1 {
    font-size: rem(60px);
}

h2 {
    font-size: em(16px);
    margin: em(63px) 0;
    grid-area: title;
    color: $clr1;
    text-transform: uppercase;
    @include type(boldCondensed);
}

h3 {
    font-size: em(29px);
}

img {
    width: 100%;
}

li {
    list-style: none;
}

p {
    margin-block-start: 0px;
    margin-block-end: 0px;
    // margin: 0 0 1em;
    max-width: 60ch;
    @include type(light);

    .main-text & {
        grid-column: 3 / 6;
        font-size: rem(22px);
        line-height: 1.384615;
        padding-inline-start: rem(30px);
        position: relative;

        &::after {
            content: "";
            position: absolute;
            display: block;
            top: 0;
            left: 0;
            width: 8px;
            height: 100%;
            max-height: 42px;
            background-color: $clr1;

            @include for-wide-layout {
                width: 10px;
            }
        }

        @include for-wide-layout {
            grid-column: 3 / 5;
            font-size: rem(26px);
        }
    }
}

.btn {
    @include btn;

    &-dark {
        @include btn($type: dark);
    }

    &-light {
        @include btn($type: light);
    }
}


// links
a {

    text-decoration: none;


}

mark {
    color: $clr1;
    background-color: transparent;
}

.go-right{
    float: right;
}

.loading-gif{
width: 100%;
height: calc(100vh - 80px);
object-fit: cover;

}